.button {
  background-color: #000;
  font-size: 2em;
  margin: 10px 0;
  color: #fff;
  padding: 1% 3%;
  border-radius: 10px;
  border: 2px solid #fff;
  cursor: pointer;
  transition: 0.5s !important;
}

.button:hover {
  background-color: #fff;
  color: #000;
}

@media (max-width: 500px) {
  .button {
    font-size: 1.6em;
  }
}
@media (max-width: 450px) {
  .button {
    font-size: 1.4em;
  }
}